.head-dw {
    height: 40px;
    /* scrollbar-width: 20px; */
}
.head-dw span {
    display: block;
    height: 43px;
    min-width: 250px;
    max-width: 400px;
    font-size: 20px;
    line-height: 43px;
    text-align: center;
    /* color: rgb(92,113,138); */
    color: rgb(255,255,255);
    overflow: hidden;
    margin-left: 20px;
    margin-right: -15px;
    border-radius: 8px;
    /* background-color: rgb(205,225,238); */
    background-color: rgb(255,255,255);
    cursor:default;
}
.head-dw .enable {
    background-color: rgb(205,225,238);
    color: rgb(92,113,138);
}
.head-dw .enable:hover {
    cursor: pointer;
}
.head-dw .selected {
    font-weight: bold;
    color: rgb(27,33,134);
}